import app from './firebase';

export const listAllRole = async () => {
  try {
    let querySnapshot = await app.firestore().collection('roles').get();
    const role = querySnapshot.docs.map((doc) => {
      return {
        id: doc.id,
        ...doc.data()
      }
    })
    return role
  } catch (err) {
    console.log(err);
  }
}


export const addRole = async (data) => {
  try {
    let role = await app.firestore().collection('roles').add(data);
    console.log(role.data);
  } catch (err) {
    console.log(err);
  }
};

export const deleteRoles = (ids) => {
  try {
    ids.map((id) => {
      app.firestore().collection('roles').doc(id).delete();
    })
  } catch (err) {
    console.log(err);
  }
};

export const getRoleApi = async (id) => {
  try {
    let querySnapshot = await app.firestore().collection('roles').doc(id).get();
    const role = querySnapshot.data()
    role.id = id
    return role
  } catch (err) {
    console.log(err);
  }
}
export const updateRole = async (data) => {
  try {
    console.log("update roles")
    let id = data.id;
    let result = await app.firestore().collection('roles').doc(id).set(data);
    console.log(result);
    return result
  } catch (err) {
    console.log(err);
  }
};
